.smtrm-admin-wrapper{
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;

}
.admin-content {
    flex: 3; /* 左カラム（メインコンテンツ） */
    padding: 20px;
    position: relative;
    
    h1{
        margin-bottom: 2em;
    }    
    .components-base-control {
        max-width: 400px;
        margin-bottom: 20px;
    }
    .components-button {
        position: absolute;
        bottom: 0;
        right: 0;
    }

}
.setting-wrapper{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    .setting-box{
        width: 400px;
    }
}
.admin-sidebar {
    flex: 1; /* 右カラム（ナビゲーション） */
    padding: 20px;
    background-color: #f1f1f1;
    border-left: 1px solid #ddd;
}

.admin-sidebar ul {
    list-style: none;
    padding-left: 0;
}

.admin-sidebar ul li {
    margin-bottom: 10px;
}

.admin-sidebar ul li a {
    text-decoration: none;
    font-weight: bold;
    color: #007cba;
}

.admin-sidebar ul li a:hover {
    text-decoration: underline;
}
.smtrm-admin-loading{
    /* 管理画面全体を中央に配置するためのスタイル */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;  /* 画面全体にスピナーを中央配置 */
    text-align: center;
    /* Spinnerのスタイル調整 */
    .custom-spinner {
        width: 100px;  /* スピナーの幅 */
        height: 100x; /* スピナーの高さ */
        border-width: 4px; /* スピナーの円の太さ */
        border-color: #3498db; /* スピナーの色 (例えば青) */
        border-right-color: transparent; /* 回転を強調するために一部透明に */
        margin: 20px auto;  /* 上下の余白と中央配置 */
        display: block; /* スピナーを中央に配置するために block に設定 */
        animation: spin 1s linear infinite;
    }
    @keyframes spin {
        from { transform: rotate(0deg); }
        to { transform: rotate(360deg); }
    }
}